*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html{
  font-family: "Open Sans", sans;
  font-weight: 400;
  font-style: normal;
  font-size: 62.5%; 
}
body{
  font-size: 2.2rem;
}

/*  */
.Appcontainer {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
/* .form_group{

} */
.form_group:not(:last-child) {
  margin-bottom: 1rem;
  margin-right: 1rem;
}
.form_group > .form_control {
  font-family: inherit;
  font-size: 2rem;
  color: inherit;
  border-radius: 1rem;
  background-color: rgba(white, 0.5);
  display: flex;
  border: solid 3px #C6DCE0;
  padding: 1rem 1.5rem;
  width: 100%;
  /* border-bottom: 3px solid black; */
}
.form_group > .form_control:focus {
  outline: none;
  /* border-bottom: 3px solid black; */
}
/* .form_group > label {
} */
label {
  font-size: 1.5rem;
  color: rgb(35, 131, 148);
  display: inline-block;
  margin-bottom: .2rem;
}
.label_filter{
  font-size: 1.6rem;
  font-weight: 600;
  color: #00596b;
  display: inline-block;
  margin-bottom: .2rem;
  padding-bottom: 1rem;
}
.heading{
    color: rgb(70, 207, 132);
    font-size: 2.5rem;
    font-weight: bold;
}
.Anualincomes{
    display: flex;
    /* flex-direction: column; */
    justify-content: space-around;
}
.Monthlyspending{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.mortgage, .bills, .spending{
    display: flex;
}
footer a:link,
footer a:visited {
  color: #4c99a3;
  /* text-decoration: none; */
}
.info {
  color: #4c99a3;
  text-align: center;
  padding: 10px;
}
/* .spendingrange{
    background-color: green;
} */
.review {
  background-color: white;
  color: black;
}
.active {
  color: green;
}
.active_danger {
  color: orangered;
}
footer span {
  color: #005669;
}
.details {
  height: 5rem;
  margin: 1rem;
  
  /* padding: 2rem; */
  /* background-color: blue; */
}
.find_ways:link, .find_ways:visited {
  background-color: transparent;
  border: 2px solid #00BA00;
  text-decoration: none;
  color: #00BA00;
  border-radius: 6px;
  padding: .2rem;
  width: 80%;
  display: block;
  text-align: center;
  margin: 0 auto;
  -webkit-transition: .4s all;
  transition: .4s all;
}
.find_ways:hover, .find_ways:active{
    background-color: #00BA00;
    color: white;
}
.saved_money{
    color:#00BA00;
    margin-right: 2px;
}
.bottom{
    display: flex;
    justify-content: flex-end;
    margin-right: 2rem;
    margin-top: 2rem;
}
.mr{
    margin: 0 1rem;
}
.form_group_row{
  flex-direction: row !important;
}
/* web */
input[type="range"] {
  -webkit-appearance: none;
  width: 100%;
  height: .5rem;
  border-radius: 5px;   
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  margin-top: 1rem;
  -webkit-transition: .2s;
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 2rem;
  height: 2rem;
  border-radius: 50%; 
  background: #00596b;
  cursor: pointer;
}

input[type="range"]::-moz-range-thumb {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background: #00596b;
  cursor: pointer;
}
.tipBox{
  display: "flex";
  align-items: "center";
  justify-content: "center";
  background-color: #005669;
  color:#fff;
  width: 20%;
  margin: 0 auto;
  border-radius: 50%;
  font-weight: bold;
  padding: 1.2rem;
}
.container {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(450px, -webkit-min-content));
  grid-template-columns: repeat(auto-fit, minmax(450px, min-content));
  justify-content: center;
  /* background-color: rgba(224, 97, 97, 0.164); */
  color: #444;
  margin: 1rem;
}
.title {
  background-color: #00596b;
  text-align: center;
  text-transform: uppercase;
  color: white;
}
.card_left,
.card_right {
  border-radius: 5px;
  border: 2px solid #72a1aa;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card_body {
  padding: 1rem;
}
@media only screen and (max-width: 700px) {
  .container {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(380px, -webkit-min-content));
    grid-template-columns: repeat(auto-fit, minmax(380px, min-content));
    margin: .5rem;
  }
 html{
   font-size: 52.2%;
 }
}

.header{
    height: 20vh;
    background-color: #4BBA00;
    color: #FFFFFF;
    padding-left: 2rem;
}
.footer{
    /* position: fixed;
    bottom: 0; */
    height: 15vh;
    width: 100%;
    background-color: black;
    color: white;
    text-align: center;
    padding: 1rem;
}
